/*Font */
body {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  color: #404152;
  background: #fff;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #060A11;
