/********** 
   Custom Styles 
**********/
/* Dropdown Styles */
.dropdown-item {
  background-color: transparent;
  border: 0;
}
.dropdown-item:hover, .dropdown-item:focus {
  color: #060a11 !important;
  background: none !important;
  background-color: #e9ecef;
}

/* Card Styles */
.card {
  border: none;
}

/* section-header style */
.section-header {
  padding: 4rem 0 3rem;
}
@media (max-width: 991.98px) {
  .section-header {
    text-align: center;
  }
}
.section-header h2 {
  position: relative;
  margin-bottom: 12px;
}
@media (max-width: 575.98px) {
  .section-header h2 {
    margin-bottom: 0;
  }
