/* load more style */
.next-more,
.load-more {
  color: #060A11;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  font-weight: 500;
}
.next-more i,
.load-more i {
  margin-right: 10px;
}

.lode-next,
.next-blog {
