  2. Button Styles 
**********/
.btn {
  letter-spacing: 0.5px;
  font-weight: 700;
}
.btn:hover {
  color: #fff;
}
.btn-check:focus + .btn, .btn:focus {
  color: #fff !important;
  outline: 0;
}
.btn-check:checked + .btn, .btn-check:active + .btn, .btn:active, .btn.active {
  color: #fff !important;
}
.btn-check:checked + .btn:focus, .btn-check:active + .btn:focus, .btn:active:focus, .btn.active:focus {
  color: #fff !important;
}
.btn:disabled, .btn.disabled, fieldset:disabled .btn {
  pointer-events: none;
  opacity: 0.1;
}
@media (max-width: 575.98px) {
  .btn {
    display: block;
    width: 80%;
    margin: 0 auto;
  }
}

.btn-primary {
  position: relative;
  color: #fff;
  height: 55px;
  text-align: center;
  line-height: 55px;
  text-transform: capitalize;
  background: #060A11;
  border: 1px solid transparent;
  overflow: hidden;
  transition: 0.7s all ease-in-out;
}
.btn-primary span {
  display: block;
  width: 100%;
  height: 100%;
  transition: 0.7s all ease-in-out;
  z-index: 2;
}
.btn-primary span svg {
  transform: translateY(-2px);
  margin-left: 5px;
}
.btn-primary span:nth-child(1) {
  transition: 0.7s all ease-in-out;
  transform: translateY(-16px);
}
.btn-primary::before {
  content: "";
  position: absolute;
  bottom: -250px;
  left: 50%;
  transform: translateX(-50%);
  width: 350px;
  height: 250px;
  background: #404152;
  border-radius: 50%;
  transition: 0.7s all ease-in-out;
}
.btn-primary:hover {
  border: 1px solid transparent;
  background: #404152;
}
.btn-primary:hover span {
  transform: translateY(-100%);
}
.btn-primary:hover span:nth-child(1) {
  transform: translateY(-150px);
}
.btn-primary:hover span:nth-child(2) {
  transform: translateY(-36px);
}
.btn-primary:hover::before {
  bottom: -150px;
}
btn-check:focus + .btn-primary, .btn-primary:focus, .btn-primary:active, .btn-primary:active:focus, .btn-primary:hover:focus {
  color: #fff !important;
  background: #060A11;
  border: 1px solid transparent;
}
.btn-secondary {
  background: rgba(7, 12, 19, 0.04);
  color: #060A11;
  border: 1px solid transparent;
  text-transform: capitalize;
  position: relative;
  height: 55px;
